import React, { Component } from 'react';

class Country extends Component {
  state = {
    name: this.props.country.name,
    goldMedalCount: this.props.country.goldMedalCount,
  }
  handleIncrement = () => this.setState({ goldMedalCount: this.state.goldMedalCount + 1 });
  handleDecrement = () => this.state.goldMedalCount === 0 ? 0 : this.setState({ goldMedalCount: this.state.goldMedalCount - 1 });
  render() { 
    return (
      <div className="country">
        <div className="name">
          { this.state.name }
        </div>
        <div className="medals">
          Gold Medals: { this.state.goldMedalCount }
          <button onClick={ this.handleIncrement }>+</button>
          <button onClick={ this.handleDecrement }>-</button>
        </div>
        <hr />
      </div>
    );
  }
}
 
export default Country;
